<template>
  <div>
    <nav :class="active">
      <li v-for="link in menu" :key="link.target">
        <button @click="fetchData(link.target, link.showModal)"><a
          href="#" :class="link.target">{{link.title}}</a></button>
      </li>
    </nav>
    <div v-if="!showModal">
      <Grid class="container-grid" :grid="grid" :calendar="calendar" :active="active"/>
    </div>
    <div v-else-if="showModal">
      <Modal @close="showModal = false" :body="data.body" :title="data.title" :subTitle1="data.subTitle1"
             :subTitle2="data.subTitle2" :body1="data.body1" :email="data.email" :link="data.link" :body2="data.body2"
             :images="images" :venue="data.venue"
             :showImages="showImages">
      </Modal>
    </div>
<div  class="fb-page-container">
    <div :class="showFb"  class="fb-page" data-href="https://www.facebook.com/festivalsLaiksDejot" data-tabs="timeline"
         data-width="500" data-height="500" data-small-header="false" data-adapt-container-width="true"
         data-hide-cover="false" data-show-facepile="true">
      <blockquote cite="https://www.facebook.com/festivalsLaiksDejot" class="fb-xfbml-parse-ignore"><a
        href="https://www.facebook.com/festivalsLaiksDejot">LAIKS DEJOT</a></blockquote>
    </div>
    <div :class="showFb"  class="fb-page" data-href="https://www.facebook.com/festivalsLaiksDejot" data-tabs="events" data-width="500"
         data-height="500" data-small-header="true" data-adapt-container-width="true" data-hide-cover="true"
         data-show-facepile="true">
      <blockquote cite="https://www.facebook.com/festivalsLaiksDejot" class="fb-xfbml-parse-ignore"><a
        href="https://www.facebook.com/festivalsLaiksDejot">LAIKS DEJOT</a></blockquote>
    </div>
</div>

  </div>
</template>

<script>
  import Modal from "./Modal";
  import Grid from "./Grid";

  export default {
    name: "Nav",
    components: {Grid, Modal},
    data: function () {
      return {

        menu: [
          {
            title: 'Par festivālu',
            url: 'https://www.google.com',
            target: 'home',
            showModal: true
          },
          {
            title: 'Programma',
            url: 'https://www.facebook.com',
            target: 'calendar',
            showModal: false
          },
          {
            title: 'Izrādes',
            url: 'https://www.yahoo.com',
            target: 'performances',
            showModal: false
          },
          {
            title: 'Meistarklases',
            url: 'https://www.flickr.com',
            target: 'workshops',
            showModal: false
          },
          {
            title: 'Biļetes',
            url: 'https://www.flickr.com',
            target: 'tickets',
            showModal: true
          },
          {
            title: 'Vietas',
            url: 'https://www.flickr.com',
            target: 'venues',
            showModal: true
          },
          {
            title: 'EN',
            url: 'https://www.flickr.com',
            target: 'EN',
            showModal: false
          }
        ],
        menu_en: [
          {
            title: 'About festival',
            url: 'https://www.google.com',
            target: 'home_en',
            showModal: true
          },
          {
            title: 'Program',
            url: 'https://www.facebook.com',
            target: 'calendar_en',
            showModal: false
          },
          {
            title: 'Performances',
            url: 'https://www.yahoo.com',
            target: 'performances_en',
            showModal: false
          },
          {
            title: 'Workshops',
            url: 'https://www.flickr.com',
            target: 'workshops_en',
            showModal: false
          },
          {
            title: 'Tickets',
            url: 'https://www.flickr.com',
            target: 'tickets_en',
            showModal: true
          },
          {
            title: 'Venues',
            url: 'https://www.flickr.com',
            target: 'venues_en',
            showModal: true
          },
          {
            title: 'LV',
            url: 'https://www.flickr.com',
            target: 'LV',
            showModal: false
          }
        ],
        menu_lv: [
          {
            title: 'Par festivālu',
            url: 'https://www.google.com',
            target: 'home',
            showModal: true
          },
          {
            title: 'Programma',
            url: 'https://www.facebook.com',
            target: 'calendar',
            showModal: false
          },
          {
            title: 'Izrādes',
            url: 'https://www.yahoo.com',
            target: 'performances',
            showModal: false
          },
          {
            title: 'Meistarklases',
            url: 'https://www.flickr.com',
            target: 'workshops',
            showModal: false
          },
          {
            title: 'Biļetes',
            url: 'https://www.flickr.com',
            target: 'tickets',
            showModal: true
          },
          {
            title: 'Vietas',
            url: 'https://www.flickr.com',
            target: 'venues',
            showModal: true
          },
          {
            title: 'EN',
            url: 'https://www.flickr.com',
            target: 'EN',
            showModal: false
          }
        ],
        grid: null,
        data: null,
        showImages: false,
        showModal: null,
        active: null,
        calendar: null,
        images: null,
        showFb: ""

      }
    },
    methods: {
      fetchData(target, showModal) {
        const url = 'https://www.laiksdejotfestivals.lv/data/';
        this.active = target;

        if (target === 'EN') {
          this.menu = this.menu_en;
          this.grid = null;
          this.calendar = null;
          this.showFb = "";
        } else if (target === 'LV') {
          this.menu = this.menu_lv;
          this.grid = null;
          this.calendar = null;
          this.showFb = "";
        } else {
          if (showModal === true) {
            fetch(url + this.active + '.json', {mode: 'cors'}).then((r) => {
              r.json().then((result) => {
                this.data = result;
                this.showModal = showModal;
                if (target === 'home') {
                  this.images = this.data.images;
                  this.showImages = true;
                  this.grid = null;
                  this.calendar = null;
                  this.showFb = "";
                } else if (target === 'home_en') {
                  this.images = this.data.images;
                  this.showImages = true;
                  this.grid = null;
                  this.calendar = null;
                  this.showFb = "";
                } else {
                  this.images = null;
                  this.showFb = "showFb";
                }
              });
            });
          }
          else {
            fetch(url + target + '.json', {mode: 'cors'}).then((r) => {
              r.json().then((result) => {
                if (target === 'calendar') {
                  this.calendar = result;
                  this.grid = null;
                  this.showFb = "showFb";
                } else if (target === 'calendar_en') {
                  this.calendar = result;
                  this.grid = null;
                  this.showFb = "showFb";
                } else {
                  this.grid = result;
                  this.calendar = null;
                  this.showImages = true;
                  this.showFb = "showFb";
                }
              });
            })
          }
        }
      }
    }
  }
</script>

<style scoped>
  .showFb {
    display: none
  }

  .fb-page-container {
    padding-left: 5%;
    display: inline-block;
    width: 90%;
  }

  .container-grid {
    padding-left: 5%;
    padding-top: 0;
    padding-bottom: 2%;
    padding-right: 25%;
  }

  .button-squared {
    white-space: normal;
    margin: 10px;
    width: 300px !important;
    height: 300px !important;
    background-color: #E3E3E3;
  }

  .image {
    float: left;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border: 1px solid #ebebeb;
    margin: 5px;
    display: block;
  }

  .inline > li {
    display: inline-block;
  }

  .button-squared > a {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding-bottom: 50%;

  }

  a:hover {
    text-decoration: none;
  }

  nav {
    display: inline-block;
    margin: 60px auto 45px;
    background-color: #172636;
    box-shadow: 0 1px 1px #ccc;
    border-radius: 2px;
  }

  nav li {
    display: inline-block;
  }

  nav a {
    display: inline-block;
    padding: 18px 30px;
    color: #fefefe !important;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none !important;
    line-height: 1;
    text-transform: uppercase;
    background-color: transparent;

    -webkit-transition: background-color 0.25s;
    -moz-transition: background-color 0.25s;
    transition: background-color 0.25s;
  }

  nav a:first-child {
    border-radius: 2px 0 0 2px;
  }

  nav a:last-child {
    border-radius: 0 2px 2px 0;
  }

  nav.home .home,
  nav.performances .performances,
  nav.calendar .calendar,
  nav.workshops .workshops,
  nav.tickets .tickets,
  nav.venues .venues,
  nav.home_en .home_en,
  nav.performances_en .performances_en,
  nav.calendar_en .calendar_en,
  nav.workshops_en .workshops_en,
  nav.tickets_en .tickets_en,
  nav.venues_en .venues_en {
    background-color: #EF3D83;
  }

  p {
    font-size: 22px;
    font-weight: bold;
    color: #7d9098;
  }

  p b {
    color: #ffffff;
    display: inline-block;
    padding: 5px 10px;
    background-color: #c4d7e0;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 18px;
  }

  h3 {
    color: #EF3D83;
  }

  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 70%;
    height: 80%;
    overflow: auto;
    margin: 0 auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }

  .modal-header h3 {
    margin-top: 0;
    color: #42b983;
  }

  .modal-body {
    margin: 20px 0;
  }

  .modal-default-button {
    float: right;
  }

  /*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

  .modal-enter {
    opacity: 0;
  }

  .modal-leave-active {
    opacity: 0;
  }

  .modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

</style>



