<template>
  <div>
    <div class="pdf"><a class="pdf-button" href="https://www.laiksdejotfestivals.lv/data/programma.pdf"><span>✓</span>Programma
      (PDF)</a></div>
    <div>
      <div class="container-grid" v-for="(day, dayIndex) in calendar">
        <div class="header accordion-header">
          {{day.date}}
        </div>
        <div class="my-content">
          <div v-for="(event, eventIndex) in day.events" class="event">
            <button class="button-squared" @click="loadFromGrid(event.target)"
                    :style="{ backgroundImage: 'url('+ imageUrl + event.target + imageEnd +')', backgroundSize: 'cover'}">
              <a href="#" :class="event.target">{{event.title}} </a></button>
            <br><a class="buy-button" v-if="event.tickets" :href=event.tickets target="_blank"><span>✓</span>
            bilesuparadize.lv</a>
            <br>{{event.chor}} <br>{{event.time}} <br>{{event.venue}}

          </div>
        </div>
      </div>
      <Modal v-if="showModal" @close="showModal = false" :body="data.body" :title="data.title" :date="data.date"
             :date1="data.date1" :date2="data.date2"
             :spot="data.spot" :crew="data.crew" :images="data.images" :showImages="showImages">
      </Modal>
    </div>
  </div>
</template>

<script>
  import Modal from "./Modal";

  export default {
    name: "Accordion",
    components: {Modal},
    props: ['calendar'],
    data: function () {
      return {
        imageUrl: "https://www.laiksdejotfestivals.lv/data/",
        imageEnd: ".png",
        data: null,
        images: null,
        showModal: false,
        showImages: true
      }
    },
    methods: {
      loadFromGrid(target) {
        const url = 'https://www.laiksdejotfestivals.lv/data/';
        fetch(url + target + '.json', {mode: 'cors'}).then((r) => {
          r.json().then((result) => {
            this.data = result;
            this.images = result.images;
            this.showModal = true;
          });
        })
      }
    }
  }
</script>

<style scoped>
  .container-grid {
    padding-bottom: 2%;
    padding-right: 10%;
  }

  .buy-button {
    text-shadow: none !important;
    float: contour;
    box-shadow: none !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #1b998b;
    border: 1px solid #1b998b;
    color: #fff;
    display: block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .pdf-button {
    background: #EF3D83;
    display: block;
    height: 20px;
    line-height: 20px;
    padding-right: 30px;
    padding-left: 70px;
    position: relative;
    color: rgb(255, 255, 255);
    text-decoration: none;
    text-transform: uppercase;
    margin-bottom: 15px;

    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    text-shadow: 0px 1px 0px rgba(0, 0, 0, 0.5);
    -ms-filter: "progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=1,Color=#ff123852,Positive=true)";
    zoom: 1;
    filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=0, OffY=1, Color=#ff123852, Positive=true);

    -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
    -ms-filter: "progid:DXImageTransform.Microsoft.dropshadow(OffX=0,OffY=2,Color=#33000000,Positive=true)";
    filter: progid:DXImageTransform.Microsoft.dropshadow(OffX=0, OffY=2, Color=#33000000, Positive=true);
  }

  .pdf-button span {
    position: absolute;
    text-align: center;
    left: 0;
    width: 25px;
    background-color: rgba(0, 0, 0, 0.5);

    -webkit-border-top-left-radius: 5px;
    -webkit-border-bottom-left-radius: 5px;
    -moz-border-radius-topleft: 5px;
    -moz-border-radius-bottomleft: 5px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-right: 1px solid rgba(0, 0, 0, 0.15);
  }

  .pdf-button:hover span, .pdf-button.active span {
    background-color: #42b983;
    border-right: 1px solid rgba(0, 0, 0, 0.3);
  }

  .accordion-header {
    text-align: center;
    color: #c9d0d4;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 38px;
    font-weight: 100;
    line-height: 50px;
    letter-spacing: 1px;
    padding: 35px 0 2px;
    border-bottom: double #555;
  }

  .pdf {
    color: #fefefe;
    float: right;
  }

  button > a.karlssakss, a.kulirosna, a.virpijunti, a.karlssakss_en, a.kulirosna_en, a.virpijunti_en {
    background-color: #369AA2;
    color: #e4e4e4;
  }

  .event {
    color: #fefefe;
    font-size: small;
    display: block;
    padding-right: 15px;
    text-align: left;

  }

  .button-squared {
    width: 200px !important;
    height: 200px !important;
    background-color: #E3E3E3;
  }

  .my-content {
    width: 210px !important;
    display: inline-grid;
    grid-template-columns: auto auto auto auto;
    grid-row-gap: 50px;
  }

  @media only screen and (max-width: 600px) {
    .my-content {
      width: 210px !important;
      display: inline-grid;
      grid-template-columns: auto;
      grid-row-gap: 50px;
    }
  }

  button > a {
    background-color: #EF3D83;
    color: #e4e4e4;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: -1px;
    line-height: 2;
    text-align: center;
    padding: 5px;
  }
</style>



